.breadcrumb {
  position: absolute;
  top: 20px;
  left: 62px;
  color: #fff;
  font-size: 12px;
  line-height: 15px;
  z-index: 1;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  li {
    & + li {
      &::before {
        content: '-';
        margin: 0 9px 0 10px;
      }
    }
  }
  a {
    color: #fff;
    text-decoration: none;
  }
}
.about-info {
  padding: 133px 0 78px;
  background: #f5f5f4;
  margin-bottom: 80px;
  transform: translateY(100px);
  opacity: 0;
  transition: all 0.3s ease;
  &.active {
    transform: translateY(0);
    opacity: 1;
  }
  @media screen and (max-width: 1250px) {
    padding: 80px 15px 78px;
  }
  @media screen and (max-width: 767px) {
    margin-bottom: 0;
  }
  &__columns {
    max-width: 972px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    @media screen and (max-width: 767px) {
      flex-wrap: wrap;
    }
  }
  &__box {
    max-width: 451px;
    flex-basis: 451px;
    width: 100%;
    @media screen and (max-width: 1023px) {
      padding: 0 15px 0 0;
    }
    @media screen and (max-width: 767px) {
      padding: 15px 0 0;
      order: 1;
    }
  }
  &__title {
    font-size: 64px;
    line-height: 73px;
    text-transform: uppercase;
    color: #402f22;
    font-family: 'LL';
    position: relative;
    margin-bottom: 13px;
    @media screen and (max-width: 1023px) {
      font-size: 44px;
      line-height: 53px;
    }
    &::after {
      content:'';
      position: absolute;
      top: 38px;
      left: -99px;
      width: 79px;
      height: 1px;
      background: #d2d2d2;
      @media screen and (max-width: 1250px) {
        display: none;
      }
    }
  }
  &__position {
    font-weight: 500;
    font-size: 14px;
    line-height: 25px;
    color: rgba(51, 51, 51, 0.6);
    max-width: 377px;
    margin-bottom: 41px;
  }
  &__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 25px;
    color: #333;
    margin-bottom: 40px;
    p {
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__bottom {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #402f22;
    text-transform: uppercase;
    .arrow-up__ico {
      margin: 0 13px 0 0;
      transform: rotate(180deg);
    }
  }
}
.interview {
  position: relative;
  padding-bottom: 70px;
  @media screen and (max-width: 767px) {
    padding: 0 0 50px;
  }
  &::after {
    content:'';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background: url('../images/decor-2.svg') no-repeat;
    height: 842px;
    background-position: 100% 0;
  }
  &::before {
    content:'';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: url('../images/decor.svg') no-repeat;
    height: 1300px;
  }
  &__box {
    max-width: 1280px;
    margin: 0 auto;
    display: flex;
    align-items: flex-end;
    position: relative;
    z-index: 1;
    &.active {
      .interview__image::after {
        width: 0;
        opacity: 0.7;
      }
      img {
        transform: translateX(0);
        opacity: 1;
      }
    }
    @media screen and (max-width: 1023px) {
      flex-wrap: wrap;
      justify-content: center;
    }
    & + .interview__box {
      margin-top: 70px;
    }
    &:nth-child(even) {
      @media screen and (min-width: 1023px) {
        .interview__image {
          order: 1;
        }
      }
      .interview__info {
        margin: 0 -127px 0 0;
        @media screen and (max-width: 1023px) {
          margin: 0;
        }
      }
      .interview__title {
        padding: 0 137px 0 62px;
        @media screen and (max-width: 1023px) {
          padding: 0;
        }
      }
      .interview__text {
        padding: 67px 199px 67px 62px;
        @media screen and (max-width: 1023px) {
          padding: 50px;
        }
        @media screen and (max-width: 767px) {
          padding: 20px;
        }
        &::after {
          right: 197px;
        }
      }
    }
    &--1,
    &--4 {
      padding-bottom: 70px;
      .interview__info {
        margin-bottom: -70px;
      }
      &:nth-child(even) {
        .interview__info {
          margin-bottom: -70px;
        }
      }
    }
    &--2 {
      align-items: flex-start;
      .interview__info {
        padding-top: 20px;
        @media screen and (max-width: 767px) {
          padding-top: 0;
        }
      }
    }
    &--3 {
      align-items: center;
    }
  }
  &__image {
    max-width: 675px;
    flex-basis: 675px;
    width: 100%;
    position: relative;
    z-index: 1;
    &::after {
      content:'';
      position: absolute;
      top: 0;
      bottom: 0;
      left:0;
      width: 100%;
      background: #fff;
      transition: all 0.8s ease;
    }
    img {
      transform: translateX(-70px);
      transition: all 0.8s ease;
      opacity: 0;
    }
    @media screen and (max-width: 1023px) {
      margin-bottom: 25px;
    }
  }
  &__info {
    max-width: 677px;
    flex-basis: 677px;
    width: 100%;
    margin: 0 0 0 -127px;
    @media screen and (max-width: 1023px) {
      margin: 0;
    }
    @media screen and (max-width: 767px) {
      padding: 0 15px;
    }
  }
  &__title {
    font-size: 20px;
    line-height: 32px;
    font-weight: 700;
    color: #402f22;
    margin-bottom: 52px;
    padding: 0 0 0 199px;
    max-width: 615px;
    position: relative;
    z-index: 1;
    @media screen and (max-width: 1023px) {
      padding: 0;
      max-width: 100%;
      margin-bottom: 25px;
    }
  }
  &__text {
    background: #f5f5f4;
    padding: 67px 62px 67px 199px;
    font-size: 16px;
    line-height: 30px;
    color: #333;
    position: relative;
    @media screen and (max-width: 1023px) {
      padding: 50px;
    }
    @media screen and (max-width: 767px) {
      padding: 20px;
    }
    &::after {
      content:'';
      position: absolute;
      top: -80px;
      right: -15px;
      background: url('../images/quote.svg') no-repeat;
      width: 90px;
      height: 90px;
      @media screen and (max-width: 1023px) {
        display: none;
      }
    }
    p {
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
.faq {
  background: #f5f5f4;
  padding: 80px 0;
  transform: translateY(100px);
  opacity: 0;
  transition: all 0.3s ease;
  &.active {
    transform: translateY(0);
    opacity: 1;
  }
  @media screen and (max-width: 1023px) {
    padding: 80px 15px;
  }
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  &__holder {
    max-width: 820px;
    margin: 0 auto 60px;
    @media screen and (max-width: 767px) {
      margin: 0 auto 50px;
    }
  }
  &__title {
    font-size: 36px;
    line-height: 41px;
    font-family: 'LL';
    text-align: center;
    text-transform: uppercase;
    color: #402f22;
    margin-bottom: 22px;
  }
  &__notes {
    font-size: 16px;
    line-height: 25px;
    color: #333;
    max-width: 630px;
    margin: 0 auto 35px;
    text-align: center;
  }
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    > li {
      background: #fff;
      box-shadow:  0px 4px 30px rgba(64, 47, 34, 0.1);
      & + li {
        margin-top: 20px;
      }
      &.active {
        .opener::after {
          background: url('../images/faq-minus.svg') no-repeat 50% 50%;
        }
      }
    }
    .opener {
      padding: 26px 66px 27px 94px;
      font-size: 16px;
      line-height: 22px;
      font-weight: 700;
      color: #333;
      display: block;
      text-decoration: none;
      position: relative;
      @media screen and (max-width: 767px) {
        padding: 26px 26px 27px 75px;
      }
      &::after {
        content:'';
        position: absolute;
        top: 50%;
        margin-top: -22px;
        left: 30px;
        background: url('../images/faq-plus.svg') no-repeat 50% 50%;
        width: 44px;
        height: 44px;
        border: 1px dashed #402f22;
        border-radius: 50%;
        @media screen and (max-width: 767px) {
          left: 15px;
        }
      }
    }
    .slide {
      font-size: 14px;
      line-height: 25px;
      color: #333;
      padding: 0 66px 33px 94px;
      @media screen and (max-width: 767px) {
        padding: 0 20px 33px 20px;
      }
      p {
        margin: 0;
      }
      ul {
        margin:0;
        padding:0;
        list-style: none;
        li {
          position: relative;
          padding-left: 15px;
          &::after {
            content:'';
            position: absolute;
            top: 10px;
            left: 0;
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background: #333;
          }
        }
      }
    }
  }
  &__callback-section {
    display: flex;
    justify-content: center;
  }
  &__callback {
    cursor: pointer;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 12px;
    line-height: 100%;
    text-transform: uppercase;
    color: #402f22;
    @media screen and (min-width: 1250px) {
      &:hover {
        .faq__callback-ico {
          &::after {
            background: #402f22;
            border: 1px solid #402f22;
          }
          path {
            fill: #fff;
          }
        }
      }
    }
  }
  &__callback-ico {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
    border: 1px dashed #402f22;
    position: relative;
    margin-right: 10px;
    border-radius: 50%;
    svg {
      position: relative;
      z-index: 1;
    }
    &::after {
      content: '';
      position: absolute;
      top: 5px;
      left: 5px;
      bottom: 5px;
      right: 5px;
      border: 1px dashed #402f22;
      border-radius: 50%;
    }
  }
}
.team {
  position: relative;
  padding: 80px 15px;
  overflow: hidden;
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  &::after {
    content:'';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: url('../images/decor-2.svg') no-repeat 100% 0;
    height: 870px;
  }
  &.bg {
    padding: 0 15px;
    background: #f5f4f4;
    &::after {
      height: 600px;
    }
  }
  &__holder {
    max-width: 1010px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    z-index: 1;
    @media screen and (max-width: 767px) {
      flex-wrap: wrap;
    }
  }
  &__info {
    max-width: 502px;
    flex-basis: 502px;
    width: 100%;
    @media screen and (max-width: 1023px) {
      margin-right: 25px;
    }
    @media screen and (max-width: 767px) {
      margin: 0 0 35px;
    }
  }
  &__title {
    font-size: 36px;
    line-height: 41px;
    color: #402f22;
    font-family: 'LL';
    margin-bottom: 22px;
    text-transform: uppercase;
    position: relative;
    @media screen and (max-width: 1023px) {
      font-size: 26px;
      line-height: 31px;
    }
    &--custom {
      font-size: 24px;
      line-height: 27px;
    }
    &::after {
      content:'';
      position: absolute;
      top: 22px;
      left: -100px;
      background: #d2d2d2;
      width: 80px;
      height: 1px;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 25px;
    color: #333;
    margin-bottom: 45px;
    @media screen and (max-width: 767px) {
      margin-bottom: 20px;
    }
    p {
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__triggers {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 14px;
    line-height: 25px;
    color: #333;
    max-width: 398px;
    flex-basis: 398px;
    width: 100%;
    li {
      display: flex;
      align-items: center;
      & + li {
        margin-top: 24px;
      }
    }
  }
  &__ico {
    width: 64px;
    height: 64px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px dashed #402f22;
    margin-right: 24px;
    flex-shrink: 0;
    font-size: 24px;
    line-height: 27px;
    font-family: 'LL';
    color: #402f22;
  }
  &__btn {
    display: block;
    max-width: 202px;
    border: 1px dashed #402f22;
    border-radius: 30px;
    padding: 6px;
    text-decoration: none;
    cursor: pointer;
    span {
      background: #402f22;
      border-radius: 30px;
      text-align: center;
      display: block;
      font-size: 12px;
      line-height: 42px;
      font-weight: 500;
      color: #fff;
      letter-spacing: 1px;
      text-transform: uppercase;
      transition: all 0.3s ease;
      border: 1px solid #402f22;
    }
    @media screen and (min-width: 1250px) {
      &:hover {
        span {
          background: none;
          color: #402f22;
        }
      }
    }
  }
}
