@mixin font($fontname, $filename, $weight, $style) {
@font-face {
  font-family: $fontname;
  src: url(#{'../fonts/'+$filename+'.eot'});
  src:
  url(#{'../fonts/'+$filename+'.eot?#iefix'}) format("embedded-opentype"),
  url(#{'../fonts/'+$filename+'.woff'}) format("woff"),
  url(#{'../fonts/'+$filename+'.ttf'}) format("truetype");
  font-style: $style;
  font-weight: $weight;
  }
}
@include font('AN', 'AvenirNextCyr-Regular', 400, normal);
@include font('AN', 'AvenirNextCyr-Medium', 500, normal);
@include font('AN', 'AvenirNextCyr-Bold', 700, normal);
@include font('LL', 'LinLibertine', 400, normal);
