.franchising-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 70px 15px;
  @media screen and (max-width: 1250px) {
    .triggers__box + .triggers__box {
      margin-left: 22px;
    }
  }
  @media screen and (max-width: 1023px) {
    .triggers__list {
      flex-wrap: wrap;
    }
    .triggers__box {
      margin: 20px;
      & + .triggers__box {
        margin: 20px;
      }
    }
  }
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  .title {
    max-width: 100%;
  }
  .home-about__text {
    max-width: 1000px;
    margin: 0 auto 70px;
    @media screen and (max-width: 767px) {
      margin: 0 auto 30px;
    }
  }
}
.franchising-about {
  background: rgba(64, 47, 34, 0.05);
  padding: 70px 0;
  transform: translateY(150px);
  transition: all 0.4s ease;
  opacity: 0;
  &.active {
    transform: translateY(0);
    opacity: 1;
  }
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  &__holder {
    max-width: 1160px;
    margin: 0 auto;
    display: flex;
    @media screen and (max-width: 1023px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__image {
    max-width: 580px;
    flex-basis: 580px;
    width: 100%;
    @media screen and (max-width: 1023px) {
      margin-bottom: 25px;
    }
  }
  &__info {
    max-width: 580px;
    flex-basis: 580px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__info-box {
    max-width: 463px;
    flex-basis: 463px;
    width: 100%;
  }
  &__title {
    font-weight: 700;
    font-size: 16px;
    line-height: 25px;
    margin-bottom: 50px;
    color: #402f22;
  }
  &__box {
    display: flex;
    align-items: flex-start;
    & + .franchising-about__box {
      margin-top: 20px;
    }
  }
  &__icon {
    width: 64px;
    height: 64px;
    border-radius: 50%;
    border: 1px dashed #402f22;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-right: 93px;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      height: 1px;
      width: 30px;
      right: -38px;
      border-top: 1px dashed #402f22;
    }
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      margin-top: -1px;
      width: 3px;
      height: 3px;
      right: -44px;
      background: #402f22;
      border-radius: 50%;
    }
  }
  &__number {
    font-size: 48px;
    line-height: 55px;
    font-family: 'LL';
    color: #402f22;
    margin-bottom: 2px;
    span {
      font-size: 25px;
      line-height: 27px;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 20px;
    color: #333;
    max-width: 192px;
  }
}
.franchising-triggers {
  padding: 70px 0;
  position: relative;
  @media screen and (max-width: 767px) {
