      height: 8px;
      @media screen and (max-width: 767px) {
        display: none;
      }
    }
    &::before {
      content:'';
      position: absolute;
      top: 0;
      left: -40px;
      background: #f5f5f4;
      width: 100px;
      height: 8px;
