      position: absolute;
      top: 2px;
      left: 2px;
      bottom: 2px;
      right: 2px;
      border-radius: 50%;
    }
    svg {
      position: relative;
      z-index: 1;
    }
  }
  &__bottom {
    display: flex;
    justify-content: center;
    padding-top: 60px;
  }
