    line-height: 20px;
    color: #333;
    margin-bottom: 6px;
    @media screen and (max-width: 1250px) {
      font-size: 16px;
    }
    @media screen and (max-width: 767px) {
      font-size: 14px;
    }
  }
  &__text {
    font-size: 12px;
