.cooperation-top {
  background: #f5f5f4;
  padding: 80px 0;
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  &__holder {
    max-width: 665px;
    margin: 0 auto;
  }
  &__title {
    font-size: 36px;
    line-height: 41px;
    color: #402f22;
    font-family: 'LL';
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 28px;
    @media screen and (max-width: 767px) {
      font-size: 26px;
      line-height: 31px;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 25px;
    color: #333;
    text-align: center;
  }
}
.formats {
  .slick-arrow {
    display: none !important;
  }
  .formats__image {
    pointer-events: none;
  }
  &__holder {
    max-width: 1160px;
    margin: 0 auto;
  }
  &__title {
    font-size: 36px;
    line-height: 41px;
    text-transform: uppercase;
    font-family: 'LL';
    color: #402f22;
    position: relative;
    margin-bottom: 60px;
    @media screen and (max-width: 1250px) {
      text-align: center;
    }
    &::after {
      content:'';
      position: absolute;
      top: 22px;
      left: -100px;
      background: #d2d2d2;
      width: 80px;
      height: 1px;
    }
  }
  &__box {
    display: flex;
    &.active {
      .formats__info {
        transform: translateX(0);
      }
      .formats__image {
        transform: translateX(0);
      }
    }
    @media screen and (max-width: 1023px) {
      flex-wrap: wrap;
      justify-content: center;
    }
    &:nth-child(odd) {
      .formats__info {
        order: 1;
      }
    }
    & + .formats__box {
      margin-top: 70px;
    }
  }
  &__info {
    max-width: 580px;
    flex-basis: 580px;
    width: 100%;
    display: flex;
    justify-content: center;
    background: #fff;
    position: relative;
    z-index: 1;
    @media screen and (min-width: 1250px) {
      transform: translateX(50%);
      transition: all 0.6s ease;
    }
    @media screen and (max-width: 1023px) {
      order: 1;
      background: #f5f5f4;
      padding: 30px 15px;
    }
  }
  &__image {
    max-width: 580px;
    flex-basis: 580px;
    width: 100%;
    .slick-arrow {
      background: #fff;
      path {
        fill: #402f22;
      }
      &.slick-prev {
        left: 27px;
      }
      &.slick-next {
        right: 27px;
      }
    }
    @media screen and (min-width: 1250px) {
      transform: translateX(-50%);
      transition: all 0.6s ease;
    }
  }
  &__info-block {
    max-width: 377px;
    flex-basis: 377px;
    width: 100%;
  }
  &__name {
    font-weight: 700;
    font-size: 16px;
    line-height: 25px;
    color: #402f22;
    margin-bottom: 23px;
  }
  &__notes {
    font-size: 14px;
    line-height: 25px;
    color: #333;
    position: relative;
    margin-bottom: 24px;
    &::after {
      content:'';
      position: absolute;
      top: 5px;
      left: -36px;
      width: 16px;
      height: 16px;
      background: url('../images/dot.png') no-repeat;
      background-size: 16px 16px;
      @media screen and (max-width: 1250px) {
        left: -24px;
      }
      @media screen and (max-width: 767px) {
        display: none;
      }
    }
  }
  &__list {
    margin: 0 0 44px;
    padding: 0;
    list-style: none;
    li {
      display: flex;
    }
  }
  &__list-name {
    position: relative;
    padding-left: 20px;
    font-size: 14px;
    line-height: 25px;
    color: #333;
    width: 60%;
  }
  &__list-ico {
    position: absolute;
    top: 7px;
    left: 0;
    img {
      vertical-align: top;
    }
    svg {
      display: block;
    }
  }
  &__list-descr {
    font-weight: 500;
    font-size: 14px;
    line-height: 25px;
    color: #333;
  }
}
