.video-section {
  position: relative;
  margin-bottom: 80px;
  height: 100vh;
  overflow: hidden;
  @media screen and (min-width: 1250px) {
    background: none !important;
  }
  @media screen and (max-width: 1250px) {
    height: auto;
    background-size: cover;
    background-repeat: no-repeat;
  }
  @media screen and (max-width: 767px) {
    margin-bottom: 50px;
  }
  &::after {
    content:'';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0,0.5);
  }
  video {
    width: 100%;
    height: auto;
    margin: -50px 0;
    @media screen and (max-width: 1439px) {
      margin: 0;
      width: 105%;
      height: 120%;
    }
    @media screen and (max-width: 1250px) {
      margin: 0;
    }
  }
  &__holder {
    position: absolute;
    top: 50%;
    left: 62px;
    transform: translateY(-50%);
    z-index: 1;
    padding-left: 100px;
    max-width: 810px;
    @media screen and (max-width: 1250px) {
      left: 15px;
    }
    @media screen and (max-width: 767px) {
      padding-left: 55px;
    }
    @media screen and (max-width: 359px) {
      padding-left: 0;
    }
    &::after {
      content:'';
      position: absolute;
      top: 29px;
      left: 0;
      background: #fff;
      height: 1px;
      width: 80px;
      @media screen and (max-width: 1023px) {
        top: 15px;
      }
      @media screen and (max-width: 767px) {
        width: 40px;
        top: 14px;
      }
      @media screen and (max-width: 359px) {
        display: none;
      }
    }
  }
  &__title {
    font-size: 48px;
    line-height: 55px;
    color: #fff;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    font-family: 'LL';
    margin-bottom: 16px;
    text-transform: uppercase;
    @media screen and (max-width: 1250px) {
      font-size: 32px;
      line-height: 45px;
    }
    @media screen and (max-width: 1023px) {
      font-size: 25px;
      line-height: 30px;
    }
    @media screen and (max-width: 767px) {
      font-size: 17px;
      line-height: 23px;
      margin-bottom: 7px;
    }
  }
  &__text {
    font-size: 18px;
    line-height: 25px;
    text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 60px;
    color: #fff;
    @media screen and (max-width: 1023px) {
      max-width: 400px;
      font-size: 16px;
      line-height: 22px;
    }
    @media screen and (max-width: 767px) {
      margin-bottom: 0;
      max-width: 270px;
      font-size: 12px;
      line-height: 18px;
    }
  }
  &__btn {
    display: block;
    max-width: 250px;
    border: 1px dashed #fff;
    border-radius: 30px;
    padding: 6px;
    text-decoration: none;
    @media screen and (max-width: 767px) {
      margin-top: 13px;
    }
    span {
      background: #fff;
      border-radius: 30px;
      text-align: center;
      display: block;
      font-size: 12px;
      line-height: 42px;
      font-weight: 500;
      color: #402f22;
      letter-spacing: 1px;
      text-transform: uppercase;
      transition: all 0.3s ease;
      border: 1px solid #fff;
      @media screen and (max-width: 767px) {
        line-height: 36px;
      }
    }
    @media screen and (min-width: 1250px) {
      &:hover {
        span {
          background: none;
          color: #fff;
        }
      }
    }
  }
  &__bottom {
    position: absolute;
    bottom: 41px;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    @media screen and (max-width: 1250px) {
      bottom: 15px;
    }
    @media screen and (max-width: 767px) {
      display: none;
    }
  }
  &__bottom-text {
    font-weight: 500;
    font-size: 10px;
    line-height: 12px;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #fff;
  }
  &__bottom-arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    border: 1px dashed #fff;
    position: relative;
    margin: 0 13px;
    &::after {
      content:'';
      position: absolute;
      top: 5px;
      left: 5px;
      bottom: 5px;
      right: 5px;
      border-radius: 50%;
      border: 1px dashed #fff;
    }
  }
}
.home-about {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 55px 0 105px;
  position: relative;
  transform: translateY(150px);
  transition: all 0.4s ease;
  opacity: 0;
  &.active {
    transform: translateY(0);
    opacity: 1;
  }
  @media screen and (max-width: 1250px) {
    padding: 0 55px 50px 105px;
  }
  @media screen and (max-width: 767px) {
    padding: 0 15px 50px;
  }
  &::after {
    content:'';
    position: absolute;
    top: 20px;
    left: 0;
    background: #d2d2d2;
    height: 1px;
    width: 80px;
    @media screen and (max-width: 767px) {
      display: none;
    }
  }
  &__title {
    font-size: 36px;
    line-height: 41px;
    font-family: 'LL';
    margin-bottom: 28px;
    text-transform: uppercase;
    color: #402f22;
    @media screen and (max-width: 767px) {
      font-size: 26px;
      line-height: 31px;
    }
  }
  &__text {
    columns: 2;
    font-weight: 500;
    font-size: 14px;
    line-height: 25px;
    color: #333;
    margin-bottom: 35px;
    @media screen and (max-width: 767px) {
      columns: 1;
    }
    p {
      margin: 0;
      & + p {
        margin-left: 40px;
        @media screen and (max-width: 1250px) {
          margin-left: 10px;
        }
        @media screen and (max-width: 767px) {
          margin-left: 0;
        }
      }
    }
  }
  &__btn {
    display: inline-flex;
    align-items: center;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #402f22;
    text-transform: uppercase;
    letter-spacing: 1px;
    cursor: pointer;
    @media screen and (min-width: 1250px) {
      &:hover {
        .home-about__icon {
          &::after {
            background: #402f22;
          }
          path {
            fill: #fff;
          }
        }
      }
    }
    @media screen and (max-width: 767px) {
      padding: 6px 20px;
      border: 1px dashed #402f22;
      border-radius: 30px;
      .home-about__icon {
        border: none;
        &::after {
          background: #402f22;
        }
        path {
          fill: #fff;
        }
      }
    }
  }
  &__icon {
    width: 34px;
    height: 34px;
    border-radius: 50%;
    border: 1px dashed #402f22;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 16px;
    position: relative;
    &::after {
      content:'';
      position: absolute;
      top: 2px;
      left: 2px;
      bottom: 2px;
      right: 2px;
      border-radius: 50%;
    }
    svg {
      position: relative;
      z-index: 1;
    }
  }
  &__image {
    margin-top: 69px;
    border-top: 1px solid #d2d2d2;
    text-align: center;
    @media screen and (max-width: 767px) {
      margin: 0;
    }
    video {
      width: 100%;
      height: auto;
    }
    img {
      vertical-align: top;
    }
  }
}
.title {
  font-size: 36px;
  line-height: 41px;
  color: #402f22;
  text-transform: uppercase;
  font-family: 'LL';
  text-align: center;
  max-width: 620px;
  margin: 0 auto 50px;
  @media screen and (max-width: 767px) {
    font-size: 26px;
    line-height: 31px;
  }
}
.triggers {
  background: #f5f5f4;
  padding: 80px 0;
  @media screen and (max-width: 1023px) {
    padding: 80px 15px;
  }
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  &__list {
    display: flex;
    justify-content: center;
    &.active {
      .triggers__box {
        opacity: 1;
      }
    }
    @media screen and (max-width: 767px) {
      flex-wrap: wrap;
    }
  }
  &__box {
    max-width: 230px;
    flex-basis: 230px;
    width: 100%;
    transition: opacity 1s ease;
    opacity: 0;
    @media screen and (max-width: 767px) {
      max-width: 100%;
    }
    & + .triggers__box {
      margin-left: 82px;
      @media screen and (max-width: 1023px) {
        margin-left: 22px;
      }
      @media screen and (max-width: 767px) {
        margin: 20px 0 0;
      }
    }
  }
  &__icon {
    width: 80px;
    height: 80px;
    display: flex;
    margin: 0 auto 50px;
    align-items: center;
    justify-content: center;
    border: 1px dashed #402f22;
    border-radius: 50%;
    position: relative;
    &::after {
      content:'';
      position: absolute;
      bottom: -36px;
      width: 1px;
      height: 30px;
      left: 50%;
      border-left: 1px dashed #402f22;
    }
    &::before {
      content:'';
      position: absolute;
      bottom: -42px;
      margin-left: -1px;
      width: 3px;
      height: 3px;
      left: 50%;
      background: #402f22;
      border-radius: 50%;
    }
    path {
      fill: #402f22;
    }
  }
  &__title {
    font-size: 48px;
    line-height: 55px;
    font-family: 'LL';
    color: #402f22;
    text-align: center;
    margin-bottom: 2px;
    span {
      font-size: 24px;
      line-height: 27px;
    }
    .count,
    .count-new {
      font-size: 48px;
      line-height: 55px;
    }

  }
  &__text {
    font-size: 14px;
    line-height: 25px;
    color: #333;
    text-align: center;
    font-family: "LL";
  }
}
.advantages {
  padding: 80px 0;
  position: relative;
  overflow: hidden;
  @media screen and (max-width: 1250px) {
    padding: 80px 15px;
  }
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  &::after {
    content:'';
    position: absolute;
    top: 0;
    left: 0;
    background: url('../images/decor.svg') no-repeat;
    background-size: 1920px auto;
    width: 1920px;
    height: 2770px;
  }
  .title {
    max-width: 900px;
  }
}
.intro-box {
  max-width: 1280px;
  margin: 0 auto;
  display: flex;
  position: relative;
  z-index: 1;
  &.active {
    .intro-box__image::after {
      width: 0;
      opacity: 0.7;
    }
    img {
      transform: translateX(0);
      opacity: 1;
    }
  }
  @media screen and (max-width: 1365px) {
    max-width: 1230px;
  }
  @media screen and (max-width: 1250px) {
    flex-wrap: wrap;
    max-width: 800px;
  }
  &:nth-child(odd) {
    .intro-box__info {
      order: 1;
      margin: 40px 0 0 -140px;
      @media screen and (max-width: 1250px) {
        margin: 40px 0 0;
      }
      &::after {
        left: auto;
        right: -13px;
        @media screen and (max-width: 1250px) {
          right: 0;
        }
      }
      &::before {
        left: 0;
        right: -13px;
        @media screen and (max-width: 1250px) {
          right: 0;
        }
      }
    }
  }
  & + .intro-box {
    margin-top: 70px;
  }
  &__image {
    flex-shrink: 0;
    position: relative;
    &::after {
      content:'';
      position: absolute;
      top: 0;
      bottom: 0;
      left:0;
      width: 100%;
      background: #fff;
      transition: all 0.8s ease;
    }
    img {
      transform: translateX(-70px);
      transition: all 0.8s ease;
      opacity: 0;
    }
    @media screen and (max-width: 1250px) {
      flex-shrink: initial;
    }
  }
  &__info {
    max-width: 626px;
    flex-basis: 626px;
    background: #fff;
    position: relative;
    z-index: 1;
    margin: 40px -140px 0 0;
    padding: 56px 71px 46px 73px;
    @media screen and (max-width: 1250px) {
      margin: 40px 0 0;
      order: 1;
      max-width: 100%;
      flex-basis: 100%;
      border: 13px solid #f5f5f4;
    }
    @media screen and (max-width: 767px) {
      padding: 25px 10px;
    }
    &::after {
      content:'';
      position: absolute;
      top: 0;
      left: -13px;
      bottom: 0;
      width: 13px;
      background: #f5f5f4;
      @media screen and (max-width: 1250px) {
        display: none;
      }
    }
    &::before {
      content:'';
      position: absolute;
      bottom: -13px;
      left: -13px;
      right: 0;
      height: 13px;
      background: #f5f5f4;
      @media screen and (max-width: 1250px) {
        bottom: 0;
        left: 0;
        display: none;
      }
    }
  }
  &__title {
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 25px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #402f22;
    margin-bottom: 33px;
    @media screen and (max-width: 767px) {
      font-size: 12px;
      line-height: 20px;
    }
  }
  &__number {
    width: 64px;
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 1px dashed #402f22;
    font-size: 24px;
    line-height: 27px;
    color: #402f22;
    font-weight: 400;
    font-family: 'LL';
    margin-right: 20px;
    flex-shrink: 0;
    @media screen and (max-width: 767px) {
      width: 44px;
      height: 44px;
      font-size: 18px;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 25px;
    color: #333;
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      li {
        position: relative;
        padding-left: 36px;
        & + li {
          margin-top: 20px;
        }
        &::after {
          content:'';
          position: absolute;
          top: 5px;
          left: 0;
          width: 16px;
          height: 16px;
          background: url('../images/dot.png') no-repeat;
          background-size: 16px 16px;
        }
      }
    }
  }
}
.franchise-section {
  position: relative;
  z-index: 1;
  padding-bottom: 80px;
  @media screen and (max-width: 1250px) {
    padding: 0 15px;
    max-width: 690px;
    margin: 0 auto;
    .title {
      text-align: left;
      font-size: 32px;
      line-height: 36px;
      padding-left: 120px;
      margin-bottom: 70px;
    }
  }
  @media screen and (max-width: 767px) {
    padding: 0 15px 50px;
    .title {
      text-align: center;
      font-size: 24px;
      line-height: 27px;
      padding-left: 0;
      margin-bottom: 50px;
    }
  }
  &__holder {
    position: relative;
    height: 636px;
    max-width: 1070px;
    margin: 0 auto;
    &.active {
      &::after {
        transform: scale(1,1);
      }
    }
    @media screen and (max-width: 1250px) {
      height: auto;
    }
    @media screen and (max-width: 767px) {
      max-width: 400px;
    }
    &::after {
      content:'';
      position: absolute;
      top: 50%;
      left: 50%;
      background: url('../images/number-image.svg') no-repeat;
      width: 459px;
      height: 466px;
      margin: -233px 0 0 -229px;
      transform: scale(-1,1);
      transition: all 0.6s ease;
      @media screen and (max-width: 1250px) {
        background: url('../images/mobile-decor.png') no-repeat;
        background-size: 120px 126px;
        width: 120px;
        height: 126px;
        top: -173px;
        left: 0;
        margin: 0;
      }
      @media screen and (max-width: 767px) {
        top: 0;
        left: 50%;
        margin-left: -60px;
      }
      @media screen and (max-width: 359px) {
        margin-left: -30px;
      }
    }
  }
  &__number {
    font-size: 18px;
    line-height: 21px;
    color: #402f22;
    font-family: 'LL';
    margin-bottom: 6px;
  }
  &__text {
    font-size: 14px;
    line-height: 20px;
    color: #333;
  }
  &__list {
    @media screen and (max-width: 1250px) {
      display: flex;
      flex-wrap: wrap;
      margin: 0 0 0 -70px;
    }
    @media screen and (max-width: 767px) {
      margin-left: -30px;
      padding-top: 160px;
      position: relative;
      &::after {
        content:'';
        position: absolute;
        top: 70px;
        left: 71px;
        border-top: 1px dashed #402f22;
        width: 47px;
      }
      &::before {
        content:'';
        position: absolute;
        top: 67px;
        left: 120px;
        width: 7px;
        height: 7px;
        border-radius: 50%;
        background: #402f22;
      }
    }
  }
  &__box {
    position: absolute;
    opacity: 0;
    transition: opacity 0.3s ease;
    &.active {
      opacity: 1;
    }
    &--1 {
      max-width: 278px;
      text-align: center;
      left: 50%;
      top: 0;
      transform: translateX(-50%);
      @media screen and (max-width: 767px) {
        &::after {
          top: -89px !important;
        }
      }
    }
    &--2 {
      max-width: 242px;
      right: 136px;
      top: 90px;
    }
    &--3 {
      max-width: 291px;
      right: 0px;
      top: 204px;
    }
    &--4 {
      max-width: 172px;
      right: 119px;
      top: 320px;
    }
    &--5 {
      max-width: 278px;
      right: 70px;
      top: 458px;
    }
    &--6 {
      max-width: 278px;
      right: 184px;
      bottom: 13px;
    }
    &--7 {
      max-width: 232px;
      text-align: right;
      left: 243px;
      bottom: 31px;
    }
    &--8 {
      max-width: 278px;
      text-align: right;
      left: 58px;
      bottom: 100px;
    }
    &--9 {
      max-width: 292px;
      text-align: right;
      left: 0;
      top: 320px;
    }
    &--10 {
      max-width: 331px;
      left: -33px;
      top: 190px;
      text-align: right;
      @media screen and (max-width: 1250px) {
        &::after {
          display: none;
        }
      }
      @media screen and (max-width: 767px) {
        height: 200px !important;
        &::after {
          display: block;
        }
      }
    }
    &--11 {
      max-width: 292px;
      text-align: right;
      left: 90px;
      top: 87px;
      @media screen and (max-width: 1250px) {
        &::after {
          display: none;
        }
      }
      @media screen and (max-width: 767px) {
        height: 100px !important;
      }
    }
    @media screen and (max-width: 1250px) {
      top: 0;
      left: 0;
      transform: none;
      max-width: 290px;
      text-align: left;
      padding: 0 0 0 50px;
      position: relative;
      margin-left: 70px;
      height: 170px;
      &::after {
        content:'';
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 1px;
        border-left: 1px dashed #402F22;
      }
      &::before {
        content:'';
        position: absolute;
        top: 0;
        left: -31px;
        z-index: 1;
        background: url('../images/mobile-circle.png') no-repeat;
        background-size: 63px 63px;
        width: 63px;
        height: 63px;
      }
    }
  }
  &__buttons {
    display: flex;
    justify-content: center;
    padding-top: 40px;
    @media screen and (max-width: 1250px) {
      position: absolute;
      right: 73px;
      bottom: 98px;
    }
    @media screen and (max-width: 767px) {
      position: relative;
      right: 0;
      bottom: 0;
    }
  }
}
.review {
  background: rgba(64, 47, 34, 0.05);
  padding: 80px 0;
  @media screen and (max-width: 1250px) {
    padding: 80px 15px;
  }
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  &__holder {
    max-width: 1070px;
    margin: 0 auto;
    padding-bottom: 65px;
    transform: translateY(100px);
    transition: all 0.6s ease;
    opacity: 0;
    &.active {
      transform: translateY(0);
      opacity: 1;
    }
  }
  &__slider {
    padding: 0 75px;
    @media screen and (max-width: 767px) {
      padding: 0;
    }
  }
  &__slide-box {
    display: flex;
    @media screen and (max-width: 1023px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__video {
    margin-right: 34px;
    @media screen and (max-width: 1023px) {
      margin: 0 0 25px;
    }
  }
  &__name {
    font-size: 14px;
    line-height: 25px;
    font-family: 'LL';
    letter-spacing: 1px;
    position: relative;
    padding-bottom: 7px;
    text-transform: uppercase;
    color: #402f22;
    margin-bottom: 15px;
    &::after {
      content:'';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 40px;
      height: 1px;
      background: #402f22;
    }
  }
  &__text {
    font-size: 12px;
    line-height: 18px;
    color: #333;
    margin-bottom: 17px;
    max-height: 116px;
    p {
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__address {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #333;
  }
  &__work-time {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #333;
  }
}
.slick-arrow {
  border: 1px solid #402f22;
  background: none;
  width: 40px;
  height: 40px;
  position: absolute;
  border-radius: 50%;
  top: 50%;
  margin-top: -20px;
  z-index: 1;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  path {
    fill: #402f22;
  }
  @media screen and (min-width: 1250px) {
    &:hover {
      background: #402f22;
      path {
        fill: #fff;
      }
    }
  }
  &.slick-next {
    right: 0;
  }
  &.slick-prev {
    left: 0;
  }
}
.slick-dots {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  bottom: -53px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  li {
    width: 12px;
    height: 12px;
    margin: 0 8px;
    &.slick-active {
      button {
        background: #402f22;
      }
    }
  }
  button {
    padding: 0;
    border: none;
    background: rgba(64, 47, 34, 0.34);
    width: 12px;
    height: 12px;
    display: block;
    border-radius: 50%;
    text-indent: -9999px;
    cursor: pointer;
  }
}
.special-section {
  padding: 80px 0 43px;
  position: relative;
  overflow: hidden;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background: url(../images/decor-2.svg) no-repeat;
    height: 842px;
    background-position: 100% 100%;
  }
  &.active {
    img {
      width: 100%;
    }
  }
  @media screen and (max-width: 1250px) {
    padding: 80px 15px 43px;
  }
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  &__holder {
    max-width: 1036px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 1;
  }
  &__image {
    margin-right: 56px;
    img {
      width: 0%;
      transition: width 0.5s ease;
      @media screen and (min-width: 1250px) {
        height: 560px;
      }
    }
    @media screen and (max-width: 1250px) {
      max-width: 500px;
    }
    @media screen and (max-width: 1023px) {
      max-width: 400px;
    }
    @media screen and (max-width: 767px) {
      display: none;
    }
  }
  &__info {
    max-width: 390px;
    flex-basis: 390px;
    width: 100%;
  }
  &__title {
    font-size: 36px;
    line-height: 41px;
    font-family: 'LL';
    margin-bottom: 22px;
    text-transform: uppercase;
    color: #402f22;
    @media screen and (max-width: 767px) {
      font-size: 26px;
      line-height: 31px;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 22px;
    color: #333;
    margin-bottom: 34px;
  }
  &__btn {
    display: block;
    max-width: 250px;
    border: 1px dashed #402f22;
    border-radius: 30px;
    padding: 6px;
    text-decoration: none;
    cursor: pointer;
    span {
      background: #402f22;
      border-radius: 30px;
      text-align: center;
      display: block;
      font-size: 12px;
      line-height: 42px;
      font-weight: 500;
      color: #fff;
      letter-spacing: 1px;
      text-transform: uppercase;
      transition: all 0.3s ease;
      border: 1px solid #402f22;
    }
    @media screen and (min-width: 1250px) {
      &:hover {
        span {
          background: none;
          color: #402f22;
        }
      }
    }
  }
}
.main-text {
  background: rgba(64, 47, 34, 0.05);
  padding: 80px 15px;
  position: relative;
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  &::after {
    content:'';
    position: absolute;
    top: 50%;
    right: 0;
    background: url('../images/decor-text.png') no-repeat;
    background-size: 154px 322px;
    width: 154px;
    height: 322px;
    margin-top: -161px;
  }
  &__holder {
    max-width: 970px;
    margin: 0 auto;
    font-size: 14px;
    line-height: 25px;
    color: #333;
  }
  h1 {
    font-size: 28px;
    line-height: 32px;
    font-family: 'LL';
    color: #402f22;
    text-transform: uppercase;
    margin: 0 0 23px;
  }
}
.footer {
  padding: 40px 15px;
  &__holder {
    max-width: 1170px;
    margin: 0 auto 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 767px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__logo {
    @media screen and (max-width: 767px) {
      display: none;
    }
  }
  &__nav {
    @media screen and (max-width: 767px) {
      width: 100%;
      margin-bottom: 30px;
    }
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      letter-spacing: 1px;
      text-transform: uppercase;
      @media screen and (max-width: 767px) {
        flex-wrap: wrap;
        justify-content: center;
      }
      li {
        @media screen and (max-width: 767px) {
          width: 100%;
          text-align: center;
        }
        & + li {
          margin-left: 44px;
          @media screen and (max-width: 767px) {
            margin: 20px 0 0;
          }
        }
      }
      a {
        color: #402f22;
        text-decoration: none;
      }
    }
  }
  &__copy {
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 767px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
}
.arrow-up {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  display: flex;
  text-transform: uppercase;
  align-items: center;
  cursor: pointer;
  letter-spacing: 1px;
  &__ico {
    width: 44px;
    height: 44px;
    border: 1px dashed #402f22;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 14px;
    position: relative;
    &::after {
      content:'';
      position: absolute;
      top: 5px;
      left: 5px;
      bottom: 5px;
      right: 5px;
      border: 1px dashed #402f22;
      border-radius: 50%;
    }
  }
}
.copy {
  font-size: 12px;
  line-height: 15px;
  color: #333;
  @media screen and (max-width: 767px) {
    width: 100%;
    text-align: center;
  }
}
.social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  @media screen and (max-width: 767px) {
    width: 100%;
    justify-content: center;
    margin: 20px 0;
  }
  li {
    & + li {
      margin-left: 16px;
    }
  }
}
.dev-link {
  text-decoration: none;
  display: flex;
  align-items: center;
  color: #333;
  font-size: 12px;
  line-height: 15px;
  &__logo {
    height: 15px;
    overflow: hidden;
    margin-left: 10px;
    max-width: 100px;
    flex-basis: 100px;
    width: 100%;
  }
  svg {
    width: 100px;
    height: 15px;
    display: block;
    transition: all 0.3s ease;
  }
  @media screen and (min-width: 1250px) {
    &:hover {
      svg {
        transform: translateY(-100%);
      }
    }
  }
}
