body.about {
  .top-page {
    @media screen and (max-width: 767px) {
      min-height: 0;
    }
  }
  .top-slider__slide-box {
    @media screen and (max-width: 767px) {
      background-position: 100% 50%;
      min-height: 0;
    }
  }
}
.top-page {
  height: 700px;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  @media screen and (max-width: 1679px) {
    height: 540px;
  }
  @media screen and (max-width: 1439px) {
    height: 510px;
  }
  @media screen and (max-width: 1250px) {
    height: 410px;
  }
  @media screen and (max-width: 1023px) {
    height: 360px;
  }
  @media screen and (max-width: 767px) {
    height: auto;
    min-height: 375px;
  }
}
.top-slider {
  &__slide {
    vertical-align: top;
  }
  &__slide-box {
    height: 700px;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    @media screen and (max-width: 1679px) {
      height: 540px;
    }
    @media screen and (max-width: 1439px) {
      height: 510px;
    }
    @media screen and (max-width: 1250px) {
      height: 410px;
      video {
        // display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    @media screen and (max-width: 1023px) {
      height: 360px;
    }
    @media screen and (max-width: 767px) {
      height: auto;
      min-height: 375px;
      display: flex;
      align-items: center;
      background-position: 50% 50%;
      .video-section__holder {
        position: relative;
        transform: none;
        padding: 60px 15px 45px 47px;
        top: 0;
        &::after {
          top: 72px;
        }
      }
    }
    &::after {
      content:'';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: linear-gradient(90deg, #000000 0%, rgba(0, 0, 0, 0.6) 50.97%, rgba(0, 0, 0, 0) 100%);;
      opacity: 0.8;
    }
  }
  .video-section__text {
    margin-bottom: 22px;
  }
  &__bottom {
    position: absolute;
    bottom: 54px;
    left: 162px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &.disabled {
      display: none;
    }
    @media screen and (max-width: 1250px) {
      bottom: 20px;
      left: 115px;
    }
    @media screen and (max-width: 767px) {
      display: none;
    }
    .slick-arrow {
      width: 44px;
      height: 44px;
      border: 1px dashed #fff;
      background: none;
      position: relative;
      top: 0;
      margin: 0;
      path {
        fill: #fff;
      }
    }
  }
  .slick-dots {
    bottom: 15px;
  }
  .slick-dots button {
    background: rgba(255,255,255,0.6);
  }
  .slick-dots li.slick-active button {
    background: #fff;
  }
}
.slide-count {
  margin: 0 20px;
}
.slide-number {
  font-size: 25px;
  line-height: 28px;
  font-family: 'LL';
  color: #fff;
}
.slide-score {
  font-size: 11px;
  line-height: 12px;
  font-family: 'LL';
  color: #fff;
  &::before {
    content: '/';
    margin-right: 2px;
  }
}
.menu-advantages {
  background: rgba(64, 47, 34, 0.05);
  padding: 80px 0;
  @media screen and (max-width: 1250px) {
    padding: 80px 15px;
  }
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  &__holder {
    max-width: 1020px;
    margin: 0 auto;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -54px 0 0 -26px;
    justify-content: center;
  }
  &__box {
    display: flex;
    align-items: flex-start;
    max-width: 320px;
    flex-basis: 320px;
    width: 100%;
    margin: 54px 0 0 26px;
    opacity: 0;
    transition: all 0.4s ease;
    &.active {
      opacity: 1;
    }
  }
  &__ico {
    max-width: 70px;
    height: 70px;
    flex-basis: 70px;
    width: 100%;
    flex-shrink: 0;
    margin-right: 20px;
    border: 1px dashed #402f22;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__title {
    text-transform: uppercase;
    font-weight: 500;
    font-size: 16px;
    line-height: 25px;
    color: #402f22;
    margin-bottom: 8px;
  }
  &__text {
    font-size: 14px;
    line-height: 25px;
    color: #333;
  }
}
.menu-description {
  padding: 80px 0;
  transform: translateY(150px);
  transition: all 0.4s ease;
  opacity: 0;
  position: relative;
  &::after {
    content:'';
    position: absolute;
    top: -140px;
    left: 0;
    background: url('../images/decor.svg') no-repeat;
    background-size: 1920px auto;
    width: 1920px;
    height: 2770px;
  }
  &.active {
    transform: translateY(0);
    opacity: 1;
  }
  @media screen and (max-width: 1023px) {
    padding: 80px 15px;
  }
  @media screen and (max-width: 767px) {
    padding: 50px 15px;
  }
  &__holder {
    max-width: 1160px;
    margin: 0 auto;
    position: relative;
    z-index: 1;
  }
  &__box {
    display: flex;
    align-items: center;
    @media screen and (max-width: 1023px) {
      flex-wrap: wrap;
      justify-content: center;
    }
    & + .menu-description__box {
      margin-top: 70px;
    }
    &:nth-child(even) {
      .menu-description__info {
        order: 1;
      }
    }
  }
  &--franchising {
    .intro-box__title {
      padding-left: 36px;
    }
    .menu-description__box {
      &:nth-child(odd) {
        .menu-description__info {
          order: 1;
        }
      }
      &:nth-child(even) {
        .menu-description__info {
          order: -1;
          @media screen and (max-width: 1023px) {
            order: 1;
          }
        }
      }
    }
  }
  &__image {
    max-width: 580px;
    flex-basis: 580px;
    width: 100%;
  }
  &__info {
    max-width: 580px;
    flex-basis: 580px;
    width: 100%;
    display: flex;
    justify-content: center;
    @media screen and (max-width: 1023px) {
      background: #f5f5f4;
      padding: 20px;
      order: 1;
    }
    .intro-box__text {
      max-width: 453px;
      flex-basis: 453px;
      width: 100%;
    }
  }
}
.products-section {
  background: #f5f4f4;
  padding: 80px 0;
  @media screen and (max-width: 1250px) {
    padding: 80px 15px;
  }
  &__holder {
    max-width: 1170px;
    margin: 0 auto;
    & + .products-section__holder {
      margin-top: 55px;
    }
  }
  &__sub-title {
    position: relative;
    display: flex;
    justify-content: center;
    font-size: 24px;
    line-height: 25px;
    font-weight: 700;
    color: #402f22;
    text-transform: uppercase;
    margin-bottom: 43px;
    @media screen and (max-width: 1250px) {
      font-size: 20px;
      line-height: 22px;
    }
    &::after {
      content:'';
      position: absolute;
      top: 12px;
      left: 0;
      right: 0;
      background: #d2d2d2;
      height: 1px;
    }
    span {
      position: relative;
      z-index: 1;
      background: #f5f4f4;
      padding: 0 20px;
    }
  }
  &__columns {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    &.active {
      .products-list {
        transform: translate(0);
        opacity: 1;
      }
      .products-slider {
        transform: translate(0);
        opacity: 1;
      }
    }
    @media screen and (max-width: 1250px) {
      flex-wrap: wrap;
      justify-content: center;
    }
    & + .products-section__columns {
      margin-top: 60px;
    }
    &.reverse {
      .products-list {
        @media screen and (min-width: 1250px) {
          order: 1;
        }
      }
    }
  }
}
.products-list {
  max-width: 570px;
  flex-basis: 570px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: -30px 0 0;
  transform: translateY(150px);
  transition: all 0.4s ease;
  opacity: 0;
  @media screen and (max-width: 1250px) {
    margin: -30px 0 30px;
  }
  @media screen and (max-width: 665px) {
    justify-content: center;
  }
}
.product {
  max-width: 270px;
  flex-basis: 270px;
  width: 100%;
  margin: 30px 0 0;
  &__image {
    margin-bottom: 14px;
  }
  &__name {
    font-size: 14px;
    line-height: 24px;
    color: #333;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 6px;
    text-align: center;
  }
  &__descr {
    font-size: 12px;
    line-height: 15px;
    color: #333;
    text-align: center;
    height: 45px;
    overflow: hidden;
  }
  &__option {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #333;
    text-align: center;
  }
}
.products-slider {
  max-width: 570px;
  flex-basis: 570px;
  width: 100%;
  background: #fff;
  padding: 70px 28px;
  transform: translateY(150px);
  transition: all 0.4s ease;
  opacity: 0;
  &__image {
    max-width: 344px;
    margin: 0 auto 38px;
    position: relative;
  }
  &__image-box {
    overflow: hidden;
    border-radius: 50%;
  }
  &__title {
    font-size: 24px;
    line-height: 25px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 18px;
    text-align: center;
    color: #333;
    min-height: 50px;
    @media screen and (max-width: 767px) {
      font-size: 16px;
      line-height: 22px;
    }
  }
  &__text {
    font-size: 18px;
    line-height: 24px;
    color: #333;
    text-align: center;
    margin-bottom: 18px;
    overflow: hidden;
  }
  &__option {
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    color: #333;
    text-align: center;
  }
  &__label {
    position: absolute;
    top: 0;
    right: 0;
    color: #fff;
    width: 100px;
    height: 100px;
    z-index: 1;
  }
  &__label-text {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    span {
      display: block;
      text-align: center;
    }
  }
  .slick-next {
    right: 20px;
  }
  .slick-prev {
    left: 20px;
  }
  .slick-dots {
    bottom: 40px;
  }
}
