* {
  &::after,
  &::before {
    box-sizing: border-box;
  }
  box-sizing: border-box;
}
body,
html {
  height: 100%;
}
body {
  margin: 0;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
  background: #fff;
  color: #797979;
  min-width: 320px;
  font: 12px/16px 'AN', sans-serif;
}
svg {
  transform: translateZ(0);
}
button:focus,
input:focus,
textarea:focus {
  outline: none;
}
button[disabled],
input[disabled],
textarea[disabled] {
  background: #fff;
}
input[type="text"],
input[type="tel"],
input[type="email"],
input[type="search"],
input[type="password"] {
  width: 100%;
  display: block;
  box-shadow: none;
  appearance: none;
  &.error {
    border-color: #f00;
  }
}
textarea {
  background: #fff;
  width: 100%;
  height: 105px;
  resize: none;
  padding: 10px;
  display: block;
  box-shadow: none;
  appearance: none;
  &.error {
    border-color: #f00;
  }
  &:focus {
    border-color: #e09a21;
  }
}
p.error {
  font-size: 13px;
  line-height: 15px;
  color: red;
}
:-moz-placeholder {
  color: rgba(61, 61, 61, 0.4);
}
::-moz-placeholder {
  color: rgba(61, 61, 61, 0.4);
  opacity: 1;
}
:-ms-input-placeholder {
  color: rgba(61, 61, 61, 0.4);
}
::-webkit-input-placeholder {
  color: rgba(61, 61, 61, 0.4);
}
input:focus::-webkit-input-placeholder {
  color: transparent;
  text-shadow: none;
}
input:focus:-moz-placeholder {
  color: transparent;
  text-shadow: none;
}
input:focus::-moz-placeholder {
  color: transparent;
  text-shadow: none;
}
textarea:focus::-webkit-input-placeholder {
  color: transparent;
  text-shadow: none;
}
textarea:focus:-moz-placeholder {
  color: transparent;
  text-shadow: none;
}
textarea:focus::-moz-placeholder {
  color: transparent;
  text-shadow: none;
}
::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}
::selection {
  background: #b3d4fc;
  text-shadow: none;
}
::-ms-clear {
  display: none;
}
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}
.center-text {
  text-align: center;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
a {
  color: #1cb531;
  text-decoration: underline;
  outline: none;
  cursor: pointer;
}
a:hover {
  text-decoration: none;
}
a:focus {
  outline: none;
}
h1 {
  color: #3d3d3d;
  font-weight: 500;
  font-size: 30px;
  line-height: 36px;
  margin: 0 0 38px;
}
h2 {
  color: #3d3d3d;
  font-weight: 500;
  font-size: 26px;
  line-height: 32px;
  margin: 0 0 38px;
  @media screen and (max-width: 767px) {
    font-size: 20px;
    line-height: 24px;
  }
}
h3 {
  color: #3d3d3d;
  font-weight: 500;
  font-size: 22px;
  line-height: 25px;
  margin: 0 0 38px;
  @media screen and (max-width: 767px) {
    font-size: 18px;
    line-height: 24px;
  }
}
h4 {
  color: #3d3d3d;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  margin: 0 0 38px;
  @media screen and (max-width: 767px) {
    font-size: 15px;
    line-height: 24px;
  }
}
.wrapper {
  width: 100%;
  overflow: hidden;
}
.error-holder {
  height: calc(100vh - 267px);
  background-size: cover;
  background-repeat: no-repeat;
  padding: 100px 62px 0;
  position: relative;
  background-position: right top;
  @media screen and (max-width: 1200px) {
    // padding: 30px 15px 0;
    // min-height: 400px;
    min-height: auto;
    height: auto;
    padding: 30px 15px;
  }
  &::after {
    content:'';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0,0.7);
  }
  &__box {
    position: relative;
    z-index: 1;
    padding-left: 100px;
    @media screen and (max-width: 767px) {
      padding: 0;
    }
  }
  &__number {
    font-size: 200px;
    line-height: 202px;
    font-family: 'LL';
    color: rgba(255,255,255,0.4);
    padding-left: 90px;
    @media screen and (max-width: 767px) {
      padding-left: 50px;
    }
  }
  &__title {
    font-size: 64px;
    line-height: 73px;
    color: #fff;
    font-family: 'LL';
    text-transform: uppercase;
    position: relative;
    margin: -75px 0 17px;
    &::after {
      content:'';
      position: absolute;
      top: 38px;
      left: -100px;
      background: #fff;
      height: 1px;
      width: 80px;
      @media screen and (max-width: 767px) {
        display: none;
      }
    }
  }
  &__text {
    color: #fff;
    font-size: 16px;
    line-height: 25px;
    margin-bottom: 33px;
  }
  &__btn {
    display: block;
    max-width: 160px;
    border: 1px dashed #fff;
    border-radius: 30px;
    padding: 6px;
    text-decoration: none;
    span {
      background: #fff;
      border-radius: 30px;
      text-align: center;
      display: block;
      font-size: 12px;
      line-height: 42px;
      font-weight: 500;
      color: #402f22;
      letter-spacing: 1px;
      text-transform: uppercase;
      transition: all 0.3s ease;
      border: 1px solid #fff;
    }
    @media screen and (min-width: 1250px) {
      &:hover {
        span {
          background: none;
          color: #fff;
        }
      }
    }
  }
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  justify-content: center;
  align-items: center;
  z-index: 10;
  display: none;
  &.active {
    display: flex;
  }
  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0,0.8);
  }
  &__close {
    position: absolute;
    top: 40px;
    right: 40px;
    cursor: pointer;
  }
  &__text {
    font-size: 14px;
    line-height: 25px;
    color: #333;
    text-align: center;
    margin-bottom: 40px;
  }
  &__holder {
    position: relative;
    z-index: 1;
    background: #fff;
    max-width: 440px;
    flex-basis: 440px;
    width: 100%;
    padding: 48px 60px;
    .home-about__btn {
      @media screen and (max-width: 767px) {
        padding: 6px 10px;
      }
    }
  }
  &__title {
    font-size: 24px;
    line-height: 27px;
    font-family: 'LL';
    margin-bottom: 20px;
    color: #402f22;
    text-transform: uppercase;
    text-align: center;
  }
  &__row {
    margin-bottom: 16px;
  }
  input {
    border: 1px solid rgba(64, 47, 34, 0.3);
    height: 36px;
    font-size: 12px;
    color: rgba(64, 47, 34, 0.6);
    padding: 0 15px;
  }
  &__row-box {
    display: flex;
    margin-bottom: 16px;
    input {
      border-left: none;
    }
  }
  form {
    margin-bottom: 30px;
  }
  .select {
    margin: 0;
    border: solid rgba(64, 47, 34, 0.3);
    border-width: 1px 0 1px 1px;
    max-width: 94px;
    flex-basis: 94px;
    width: 100%;
    flex-shrink: 0;
    padding: 8px 16px;
    font-size: 12px;
    line-height: 15px;
    color: #402F22;
    &::before {
      content:'';
      position: absolute;
      top: 50%;
      right: 0;
      background: rgba(64, 47, 34, 0.3);
      width: 1px;
      height: 24px;
      margin-top: -12px;
    }
    img {
      margin-right: 8px;
      vertical-align: top;
    }
    &::after {
      border-bottom: 1px solid #999;
      border-right: 1px solid #999;
      height: 5px;
      width: 5px;
      margin-top: -3px;
      right: 13px;
    }
  }
  .select__current {
    display: flex;
    align-items: center;
  }
  &__btn {
    border: 1px dashed #402f22;
    border-radius: 30px;
    height: 42px;
    color: #402f22;
    font-weight: 500;
    font-size: 12px;
    font-family: 'AN';
    text-transform: uppercase;
    text-align: center;
    padding: 0;
    max-width: 156px;
    width: 100%;
    margin: 0 auto;
    display: block;
    background: none;
    cursor: pointer;
    transition: all 0.3s ease;
    @media screen and (min-width: 1250px) {
      &:hover {
        background: #402f22;
        color: #fff;
        border: 1px solid #402f22;
      }
    }
  }
  &__notes {
    display: flex;
    justify-content: center;
    position: relative;
    margin-bottom: 18px;
    &::after {
      content:'';
      position: absolute;
      top: 9px;
      left: 0;
      right: 0;
      background: #d2d2d2;
      height: 1px;
    }
    span {
      position: relative;
      background: #fff;
      z-index: 1;
      padding: 0 10px;
    }
  }
  &.popup-formats {
    z-index: 9;
    .popup__holder {
      padding: 16px;
      max-width: 1040px;
      flex-basis: 1040px;
    }
    .formats__image {
      max-width: 536px;
      flex-basis: 536px;
      @media screen and (max-width: 767px) {
        display: none;
      }
    }
    .formats__name {
      margin-bottom: 20px;
    }
    .formats__notes {
      margin-bottom: 20px;
      &::after {
        display: none;
      }
    }
  }
}
.soc-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  li {
    margin: 0 9px;
  }
}
.cancel {
  border: 1px dashed #402F22;
  border-radius: 30px;
  font-weight: 500;
  font-size: 12px;
  color: #402f22;
  line-height: 40px;
  max-width: 156px;
  margin: 0 auto;
  text-align: center;
  cursor: pointer;
}
