body.home .header {
  position: absolute;
  @media screen and (max-width: 1250px) {
    position: relative;
  }
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 7px 62px;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  background: #fff;
  @media screen and (max-width: 1250px) {
    padding: 7px 15px;
  }
  @media screen and (max-width: 767px) {
    padding: 7px 57px 7px 15px;
  }
  &__holder {
    display: flex;
    align-items: center;
    max-width: 100%;
    flex-basis: 100%;
  }
  &__navigation {
    display: flex;
    align-items: center;
    flex-basis: 560px;
    max-width: 560px;
    @media screen and (max-width: 767px) {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      background: #fff;
      transform: translateX(-100%);
      height: 100vh;
      z-index: 5;
      &.active {
        transform: translateX(0);
        display: block;
      }
    }
  }
  &__navigation-box {
    display: flex;
    align-items: center;
    width: 100%;
    @media screen and (max-width: 767px) {
      display: block;
      padding: 50px 15px 0;
    }
  }
}
.franchising {
  font-weight: 500;
  font-size: 12px;
  line-height: 40px;
  text-align: center;
  color: #402f22;
  border: 1px dashed #402f22;
  border-radius: 30px;
  text-decoration: none;
  max-width: 156px;
  flex-basis: 156px;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-right: 40px;
  transition: all 0.3s ease;
  @media screen and (min-width: 1250px) {
    &:hover {
      background: #402f22;
      border: 1px solid #402f22;
      color: #fff;
    }
  }
  @media screen and (max-width: 1023px) {
    margin-right: 33px;
    max-width: 130px;
  }
  @media screen and (max-width: 767px) {
    line-height: 50px;
    max-width: 200px;
    font-size: 14px;
    flex-basis: 200px;
    width: 100%;
    display: block;
    margin: 0 auto 15px;
    font-weight: bold;
  }
}
.logo {
  margin-right: 45px;
  @media screen and (max-width: 1023px) {
    margin-right: 10px;
    max-width: 150px;
  }
  &--mobile {
    display: none;
    @media screen and (max-width: 767px) {
      display: block;
      max-width: 80px;
      margin: 0 auto 30px;
    }
  }
}
.mobile-tel {
  display: none;
  position: absolute;
  bottom: 15%;
  left: 0;
  right: 0;
  justify-content: center;
  @media screen and (max-width: 767px) {
    display: flex;
  }
  a {
    color: #402f22;
    font-size: 20px;
    line-height: 22px;
    font-weight: 700;
    text-decoration: none;
    display: flex;
    align-items: center;
    svg {
      margin-right: 10px;
      width: 15px;
      height: 15px;
    }
  }
}
.nav {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 1px;
  @media screen and (max-width: 767px) {
    font-size: 20px;
    line-height: 22px;
    width: 100%;
    flex-shrink: 0;
    font-weight: normal;
    text-align: center;
  }
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    @media screen and (max-width: 767px) {
      display: block;
    }
    li {
      & + li {
        margin-left: 40px;
        @media screen and (max-width: 1023px) {
          margin-left: 15px;
        }
        @media screen and (max-width: 767px) {
          margin: 15px 0 0;
        }
      }
    }
    a {
      color: #402f22;
      text-decoration: none;
    }
  }
}
.phones {
  display: flex;
  align-items: center;
  align-items: flex-end;
  flex-direction: column;
  flex-shrink: 0;
  &__row {
    margin-bottom: 5px;
    a {
      color: #402f22;
      font-size: 14px;
      line-height: 17px;
      font-weight: 700;
      text-decoration: none;
      display: flex;
      align-items: center;
      svg {
        margin-right: 6px;
      }
    }
  }
}
.callback {
  font-size: 11px;
  line-height: 13px;
  color: #402f22;
  cursor: pointer;
  border-bottom: 1px dashed;
  display: inline-flex;
  letter-spacing: 1px;
  @media screen and (min-width: 1250px) {
    &:hover {
      border-color: transparent;
    }
  }
}
.menu-opener {
  width: 26px;
  height: 19px;
  right: 15px;
  padding: 0 0 0 26px;
  position: relative;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  line-height: 42px;
  transition: background 0.1s ease-in-out;
  z-index: 6;
  position: fixed;
  top: 24px;
  display: none;
  @media screen and (max-width: 767px) {
    display: block;
  }
  &__line {
    background: #402f22;
    width: 25px;
    height: 2px;
    position: absolute;
    left: 0;
    transition: all 0.1s ease-in-out;
  }
  .line-1 {
    top: 0;
  }
  .line-2 {
    top: 6px;
  }
  .line-3 {
    top: 12px;
  }
  &.active .line-1 {
    transform: rotate(45deg);
    box-shadow: none;
    top: 6px;
  }
  &.active .line-2 {
    display: none;
  }
  &.active .line-3 {
    transform: rotate(-45deg);
    box-shadow: none;
    top: 6px;
  }
  &__text {
    color: #fff;
    text-transform: uppercase;
    display: block;
    transition: color 0.1s ease-in-out;
  }
}
